*{
    margin: 0;
    padding: 0;
}
#hBtn:hover{
    background-color: plum;
    border-radius: 100%;
}

#R{
    height: 40px;
    border-radius: 20%;
}

#menu{
    display: none;
}
#navbar{
    display:flex;
    position:fixed;
    width:100%;
    background-color:rgb(232, 231, 231);
    min-width:max-content;
    align-items:center;
    color:black;
    padding: 0px 30px;
    z-index:999;
    gap:2
}

#navbar p {
    color: navy;
    font-weight: 600;
    font-size: 18px;
}

#resume:hover{
 background-color:orangered;
 color:whitesmoke;
}

#home{
    height: 100vh;
    color: black;
    
}

#hi{
    height: 60px;
    margin-left: 10px;
    margin-right: 10px;
   }

#type{
    display: block;
    margin: auto;
    height: 110px;
    
}

#home  > div{
    padding-top: 3%;
    width: 85%;
    margin: auto;
    text-align: center;
}


.typed-out{
    overflow: hidden;
    text-align: center;
    margin: auto;
    border-right: 0px solid crimson;
    white-space: nowrap;
    animation: typing 2s steps(10, end) forwards;
  }
  @keyframes typing {
    50% { width: 0 }
    100% { width: 82% }
  }


#resume{
    padding:20px 35px;
    background-color:whitesmoke;
    color: orangered;
    margin: auto;
    display: flex;
    font-size: 24px;
    font-family: 'Courier New', Courier, monospace;
    text-align: center;
    border: 1px solid orangered;
}


#about{ 
    padding-top: 4.5%;
    padding-bottom: 5%;
    color: black;
    height: 100vh;
    align-items: center;
    justify-content: space-around;
    
}

#about-para{
    display: flex;
    align-items: center;
    justify-content: space-around;
    text-align: justify;
    padding: 0% 10%;
  
}

#about-para p{
    font-size: 20px;
    width: 90%;
    font-family:monospace;
    font-weight: 700;
    
}

#about-para > div:nth-child(1){
    /* border: 1px solid; */
    width: 80%;
}

#profile{
    height: 250px;
    width: 100%;
    border-radius: 20px;
}


#skills{
    height: 100vh;
    padding-top: 4.5%;
    padding-bottom: 5.5%;
    color: black;
    /* background: linear-gradient(skyblue, white, slateblue) */
}

#skills-grid{
    display: grid;
    grid-template-columns: repeat(6,1fr);
    gap: 20px;
    width: 85%;
    margin: auto;
   
}

#skills-grid > div{
    /* border: 1px solid ; */
    position: relative;
    border-radius: 10px;
    display: flex;
    flex-direction: column;
    justify-content: space-around;
    align-items: center;
    font-size: 16px;
    color: black;
    font-family:'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;
    font-weight: 700;

      
}


#skills-grid > div > img{
    height: 50%;
    border-radius: 15px;
}

#skills-grid >div:hover{
    padding: 2.5%;
    box-shadow: rgba(11, 35, 43, 0.868) 0px 2px 4px 0px, rgba(21, 21, 57, 0.658) 0px 2px 16px 0px;
}




#projects{
     padding-top: 4.5%;
    text-align: center;
    color: #000;
}

#project_grid{
    display: grid;
    width: 85%;
    margin: auto;
    grid-template-columns: repeat(2,1fr);
    gap: 70px
}

#project_grid >div{
    padding: 20px;
    background-color: gray;
    border-radius: 10px;
}

#project_grid >div:hover{
    background-color: silver;
    color: black;
    padding: 2%;
    box-shadow: rgba(0, 0, 0, 0.25) 0px 14px 28px, rgba(0, 0, 0, 0.22) 0px 10px 10px;
}

#project_grid >div> div{
    overflow: hidden;
    

}

#project_grid >div> div> img{
    /* border: 1px solid; */
    height: 101%;
}

.btn {
    display: flex;
    justify-content: space-around;
    width: 40%;
    margin: auto;
    margin-top: 20px;
    /* border: 1px solid red; */
}

.btn > button{
    font-size: 40px;
    background:none;
}

.btn > a{
    color: black;
}

.btn > button:hover{
    background:none;
}

#sep{
    background-color: gray;
    height: 20px;
    display: flex;
    justify-content: center;
    align-items: center;
    font-weight: 700;
    font-size: 12px;
    color: silver;
    border-bottom-left-radius: 5px;
    border-bottom-right-radius: 5px;
}

#screen{
    border-top: 10px solid black;
    border-left: 10px solid black;
    border-right: 10px solid black;
    border-top-left-radius: 10px;
    border-top-right-radius: 10px;
    height: 300px;
} 
#screen img{
    height: 100%;
}

#tech_stack{
    display: flex;
    gap: 10px;
    justify-content: center;
    align-items: center;
    margin-bottom: 15px;
    margin-top: 15px;

}

#tech_stack > img{
   height: 40px;
    
}

#calender{
    margin: auto;
    align-items: center;
    /* border: 1px solid; */
    border-radius: 10px;
    width: 70%;
    padding: 20px;
    margin-bottom: 2%;
}

#github{
    /* padding-top: 4.5%;
    padding-bottom: 5%; */
    color: #000;
}

#contact{ 
    padding-bottom: 2%;
    color: #000;
}

#contact_grid{
    display: flex;
    justify-content: space-evenly;
    width: 40%;
    /* border: 1px solid; */
    margin: auto;
       
}

#contact_grid > div{
    /* border: 1px solid grey; */
    padding: 10px;
    border-radius: 10px;
}

#contact_grid i{
    font-size: 35px;
}




/* =============> Media Queries <================ */

/* Position and sizing of burger button */
.bm-burger-button {
    position:fixed;;
    width: 36px;
    height: 20px;
    left: 80%;
    top: 20px;
  }
  
  /* Color/shape of burger icon bars */
  .bm-burger-bars {
    background: yellowgreen;
  }
  
  .bm-cross {
    background: red;
  }
  
 
  .bm-menu-wrap {
    position: fixed;
    /* height: 100%; */
    top: 0px;
    
  }
  
  /* General sidebar styles */
  .bm-menu {
    background: rgba(220, 220, 220, 0.538);
    padding: 2.5em 1.5em 0;
    font-size: 1.15em;
    
  }
  
  /* Morph shape necessary with bubble or elastic */
  .bm-morph-shape {
    fill: #373a47;
  }
  
  /* Wrapper for item list */
  .bm-item-list {
    color: #000;
    font-size: large;
    font-weight: 700;
    padding: 0.8em;
    ;
  }
  
  /* Individual item */
  .bm-item {
    display: inline-block;
  }
  
  /* Styling of overlay */
  .bm-overlay {
    background: rgba(0, 0, 0, 0.3);
    top: 0px;
  }
@media all and (min-width: 125px) and (max-width:426px){

  #menu{
    display: flex;
    align-items: center;
    height: 60px;
  }

    #navbar{
        display: none;
    }
    
    #home{
        width: 100%;
        height: 100vh;
        padding-top: 20%;
    }

    #home > div> div:nth-child(1) {
        /* border: 1px solid; */
        width: 95%;
        margin-bottom: -20px;
    }

   #hi{
    height: 30px;
   }
    
    #about{
        width: 100%;
        height: 100vh;
    }

    #about p{
        font-size: 10px;
        width: 98%;
    }

    #about img{
        width: 200px;
        height: 200px;
    }

    #about-para{
        display: flex ;
        flex-direction:column;
        text-align: justify;
        gap: 40px;


    }

    #home h3{
        font-size: 25px;
        margin-bottom: -20px;
    } 
    #home h1{
        font-size: 38px;
    }

    #skills, #projects{
        height: auto;
        padding-top: 15%;
        padding-bottom: 25%;
    }
    
    #skills-grid{
        grid-template-columns: repeat(2, 1fr);
    }

    #project_grid{
        grid-template-columns: repeat(1, 1fr);
    }

    #project_grid > div{
       margin-bottom: 10%;
    }

    #project_grid > div > div p{
        font-size: 12px;
        width: 90%;
        margin: auto;
    }

    #project_grid > div > div h2{
        font-size: 20px;
    }

    #screen{
        height: 50%;
        
    }

    #tech_stack{
        display: flex;
        gap: 10px;
        justify-content: center;
        align-items: center;
        margin-bottom: 15px;
        margin-top: 15px;
    
    }

    #tech_stack > h3{
        font-size: 15px;
        font-family:monospace;
    }
    
    #tech_stack > img{
       height: 25px;
        
    }
    i{
        font-size: 30px;
    }

}

@media all and (min-width: 427px) and (max-width:769px){

    #menu{
        display: none;
    }

    #home{
        width: 100%;
        height: 100vh;
        padding-top: 15%;
    }
    
    #about{
        width: 100%;
        height: 100vh;
    }

    #about img{
        height: 250px;
    }

    #about p{
        font-size: 18px;
        width: 98%;
    }

    #about-para{
        display: flex ;
        flex-direction: column-reverse;
        gap: 40px;

    }

    #home h3{
        font-size: 20px;
        margin-bottom: -20px;
    } 
    #home h1{
        font-size:60px;
    }

    #skills, #projects{
        height: auto;
        padding-top: 8%;
        padding-bottom: 25%;
    }
    
    #skills-grid{
        grid-template-columns: repeat(3, 1fr);
    }

    #project_grid{
        grid-template-columns: repeat(1, 1fr);
    }

    #project_grid > div{
       margin-bottom: 10%;
    }

    #project_grid > div > div p{
        font-size: 16px;
        width: 90%;
        margin: auto;
    }

    #project_grid > div > div h2{
        font-size: 30px;
    }

    #screen{
        height: 70%;
        
    }

    #screen img{
        width: 100%;
    }

    #tech_stack{
        display: flex;
        gap: 10px;
        justify-content: center;
        align-items: center;
        margin-bottom: 30px;
        margin-top: 15px;
    
    }

    #tech_stack > h3{
        font-size: 18px;
        font-family:monospace;
    }
    
    #tech_stack > img{
       height: 45px;
        
    }

    .btn{
        font-size: 30px;

        /* background-color: #fff; */

    }



}